import React, { useEffect } from "react";
import {
  TreePine,
  Store,
  Building2,
  Home,
  Heart,
  GraduationCap,
} from "lucide-react";
import { Card, CardContent } from "../ui/card";

const SupportList = () => {
  // 
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const categories = [
    {
      id: "housing",
      title: "",
      path: "/housing-support",
      icon: Home,
      iconColor: "text-yellow-600",
      bgColor: "bg-yellow-50",
      items: [
        { id: 1, title: "" },
        { id: 2, title: "" },
        { id: 3, title: "" },
        { id: 4, title: "" },
      ],
    },
    {
      id: "agriculture",
      title: "",
      path: "/agriculture-support", // 
      icon: TreePine,
      iconColor: "text-green-600",
      bgColor: "bg-green-50",
      items: [
        { id: 5, title: "" },
        { id: 6, title: "" },
        { id: 7, title: "" },
        { id: 8, title: "" },
        { id: 9, title: "" },
        { id: 10, title: "" },
        { id: 11, title: "" },
      ],
    },
    {
      id: "commerce",
      title: "",
      path: "/commerce-support", // 
      icon: Store,
      iconColor: "text-blue-600",
      bgColor: "bg-blue-50",
      items: [
        { id: 12, title: "" },
        { id: 13, title: "" },
        { id: 14, title: "" },
      ],
    },
    {
      id: "construction",
      title: "",
      path: "/construction-support",
      icon: Building2,
      iconColor: "text-gray-600",
      bgColor: "bg-gray-50",
      items: [
        { id: 15, title: "" },
        { id: 16, title: "" },
        { id: 17, title: "" },
        { id: 18, title: "" },
        { id: 19, title: "" },
      ],
    },
    {
      id: "welfare",
      title: "",
      path: "/welfare-support",
      icon: Heart,
      iconColor: "text-pink-600",
      bgColor: "bg-pink-50",
      items: [
        { id: 20, title: "" },
        { id: 21, title: "" },
        { id: 22, title: "" },
        { id: 23, title: "" },
        { id: 24, title: "" },
        { id: 25, title: "" },
        { id: 26, title: "" },
        { id: 27, title: "" },
        { id: 28, title: "" },
        { id: 29, title: "" },
      ],
    },
    {
      id: "education",
      title: "",
      path: "/education-support",
      icon: GraduationCap,
      iconColor: "text-purple-600",
      bgColor: "bg-purple-50",
      items: [
        { id: 30, title: "" },
        { id: 31, title: "" },
        { id: 32, title: "" },
        { id: 33, title: "" },
        { id: 34, title: "" },
        { id: 35, title: "" },
        { id: 36, title: "" },
        { id: 37, title: "" },
        { id: 38, title: "" },
        { id: 39, title: "ICT" },
      ],
    },
  ];

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="container mx-auto px-4">
        <h1 className="text-3xl font-bold text-center mb-12"></h1>

        <div className="space-y-12">
          {categories.map((category) => {
            const Icon = category.icon;
            return (
              <div
                key={category.id}
                className="bg-white rounded-lg shadow-sm p-6"
              >
                <div className="flex items-center gap-4 mb-6">
                  <div className={`p-3 rounded-lg ${category.bgColor}`}>
                    <Icon className={`w-8 h-8 ${category.iconColor}`} />
                  </div>
                  <h2 className="text-2xl font-bold">{category.title}</h2>
                </div>

                <div className="grid gap-4">
                  {category.items.map((item) => (
                    <Card
                      key={item.id}
                      className="hover:shadow-md transition-shadow duration-200 cursor-pointer"
                      onClick={() =>
                        window.open(
                          `${category.path}#support-${item.id}`,
                          "_blank"
                        )
                      }
                    >
                      <CardContent className="p-4">
                        <div className="flex justify-between items-center">
                          <span>{item.title}</span>
                          <button
                            className="text-blue-600 hover:text-blue-800"
                            onClick={(e) => {
                              e.stopPropagation();
                              window.open(
                                `${category.path}#support-${item.id}`,
                                "_blank"
                              );
                            }}
                          >
                            
                          </button>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default SupportList;
