export const calculateSupportPrograms = (answers) => {
  const applicablePrograms = [];

  // supportCalculator.js timing

  //  -------------------------------------------------------------
  if (answers.living_plan === "new_house") {
    applicablePrograms.push({
      id: "new_house_support",
      name: "",
      amount: 3000000,
      description:
        "300100",
      category: "housing",
      subcategory: "construction",
      duration: "",
      timing: 2, // 2
    });
  }

  if (answers.living_plan === "rent") {
    applicablePrograms.push({
      id: "rent_support",
      name: "",
      amount: 1500000,
      description: "5",
      category: "housing",
      subcategory: "rent",
      duration: "5",
      timing: 5, // 
    });
  }

  // 
  if (answers.basic_info !== "none") {
    applicablePrograms.push({
      id: "moving_support",
      name: "",
      amount: 160000,
      description: "",
      category: "living",
      subcategory: "moving",
      duration: "",
      timing: 0, // 
    });
  }

  //  -------------------------------------------------------------
  switch (answers.work_style) {
    case "agriculture_new":
      applicablePrograms.push({
        id: "new_farmer_support",
        name: "",
        amount: 2400000,
        description: "1202",
        category: "industry",
        subcategory: "agriculture",
        duration: "2",
        timing: 1, // 1
      });
      break;

    case "agriculture_successor":
      applicablePrograms.push({
        id: "agriculture_successor_support",
        name: "",
        amount: 2400000,
        description: "54",
        category: "industry",
        subcategory: "agriculture",
        duration: "4",
        timing: 1, // 1
      });
      break;

    case "business":
      applicablePrograms.push({
        id: "business_support",
        name: "",
        amount: 2000000,
        description: "",
        category: "industry",
        subcategory: "business",
        duration: "",
        timing: 3, // 1
      });
      break;

    case "business_product":
      applicablePrograms.push({
        id: "product_development_support",
        name: "",
        amount: 1000000,
        description: "",
        category: "industry",
        subcategory: "business",
        duration: "",
        timing: 2, // 2
      });
      break;

    default:
      // 
      break;
  }

  //  ---------------------------------------------------------------
  if (
    answers.basic_info === "family_young" ||
    answers.basic_info === "family_school"
  ) {
    // 
    applicablePrograms.push({
      id: "child_medical_support",
      name: "",
      amount: 0,
      description: "",
      category: "living",
      subcategory: "medical",
      duration: "22331",
      timing: 0,
      isVariable: true,
      note: "",
    });

    applicablePrograms.push({
      id: "childcare_fee_support",
      name: "",
      amount: 0,
      description: "23",
      category: "living",
      subcategory: "childcare",
      duration: "3",
      timing: 0,
      isVariable: true,
      note: "",
    });

    applicablePrograms.push({
      id: "therapy_transport_support",
      name: "",
      amount: 0,
      description:
        "",
      category: "living",
      subcategory: "medical",
      duration: "18331",
      timing: 0,
      isVariable: true,
      note: "21",
    });

    applicablePrograms.push({
      id: "flu_vaccine_support",
      name: "",
      amount: 0,
      description: "18",
      category: "living",
      subcategory: "medical",
      duration: "18331",
      timing: 0,
      isVariable: true,
      note: "",
    });

    // family_young
    if (answers.basic_info === "family_young") {
      applicablePrograms.push({
        id: "first_prenatal_visit_support",
        name: "",
        amount: 0,
        description: "",
        category: "living",
        subcategory: "medical",
        duration: "",
        timing: 0,
        isVariable: true,
        note: "\n",
      });

      applicablePrograms.push({
        id: "pregnancy_support",
        name: "",
        amount: 0,
        description:
          "",
        category: "living",
        subcategory: "medical",
        duration: "",
        timing: 0,
        isVariable: true,
        note: "142\n1515,000",
      });

      applicablePrograms.push({
        id: "postpartum_care_support",
        name: "",
        amount: 0,
        description: "6",
        category: "living",
        subcategory: "childcare",
        duration: "6",
        timing: 0,
        isVariable: true,
        note: "7\n",
      });

      applicablePrograms.push({
        id: "childbirth_support",
        name: "",
        amount: 200000,
        description: "",
        category: "living",
        subcategory: "childcare",
        duration: "",
        timing: 3,
      });

      // 
      applicablePrograms.push({
        id: "mumps_vaccine_support",
        name: "",
        amount: 0,
        description:
          "1",
        category: "living",
        subcategory: "medical",
        duration: "1",
        timing: 0,
        isVariable: true,
        note: "",
      });

      // 
      applicablePrograms.push({
        id: "fertility_treatment_support",
        name: "",
        amount: 0,
        description: "",
        category: "living",
        subcategory: "medical",
        duration: "",
        timing: 0,
        isVariable: true,
        note: "75%\n\n",
      });
    }
  }

  //  --------------------------------------------------

  // 
  if (
    answers.basic_info !== "none" &&
    answers.scholarship_status === "has_scholarship"
  ) {
    applicablePrograms.push({
      id: "scholarship_support",
      name: "",
      amount: 2400000,
      description: "210",
      category: "industry",
      subcategory: "education",
      duration: "10",
      timing: 0,
    });
  }

  // 
  if (
    answers.basic_info === "family_young" ||
    answers.basic_info === "family_school"
  ) {
    // 
    applicablePrograms.push({
      id: "school_lunch_support",
      name: "",
      amount: 0,
      description: "3",
      category: "living",
      subcategory: "education",
      duration: "3",
      timing: 0,
      isVariable: true,
      note: "\n",
    });

    // 
    applicablePrograms.push({
      id: "athlete_support",
      name: "",
      amount: 0,
      description: "",
      category: "living",
      subcategory: "education",
      duration: "",
      timing: 0,
      isVariable: true,
      note: "80%\n\n",
    });

    // 
    applicablePrograms.push({
      id: "school_transport_support",
      name: "",
      amount: 0,
      description: "3km",
      category: "living",
      subcategory: "education",
      duration: "",
      timing: 0,
      isVariable: true,
      note: "1002510\n",
    });

    // 
    applicablePrograms.push({
      id: "high_school_transport_support",
      name: "",
      amount: 0,
      description:
        "",
      category: "living",
      subcategory: "education",
      duration: "",
      timing: 0,
      isVariable: true,
      note: "JR\n",
    });

    // 
    applicablePrograms.push({
      id: "high_school_comprehensive_support",
      name: "",
      amount: 0,
      description:
        "",
      category: "living",
      subcategory: "education",
      duration: "",
      timing: 0,
      isVariable: true,
      note:
        "\n" +
        "46,000\n" +
        "90,000\n" +
        "70,000\n" +
        "\n" +
        "45,000\n" +
        "80%\n" +
        "",
    });

    // 
    applicablePrograms.push({
      id: "high_school_club_activity_support",
      name: "",
      amount: 0,
      description: "",
      category: "living",
      subcategory: "education",
      duration: "",
      timing: 0,
      isVariable: true,
      note: "\n",
    });

    // ICT
    applicablePrograms.push({
      id: "high_school_ict_support",
      name: "ICT",
      amount: 0,
      description: "11iPad",
      category: "living",
      subcategory: "education",
      duration: "",
      timing: 0,
      isVariable: true,
      note: "iPad\ne-\n",
    });

    // 
    applicablePrograms.push({
      id: "high_school_international_exchange",
      name: "",
      amount: 580000,
      description: "10",
      category: "living",
      subcategory: "education",
      duration: "",
      timing: 2,
      note: "23\n\n",
    });
  }

  //  ----------------------------------------------------------------
  const totalAmount = applicablePrograms.reduce(
    (sum, program) => sum + program.amount,
    0
  );

  return {
    programs: applicablePrograms,
    totalAmount: totalAmount,
    userProfile: {
      basic_info: answers.basic_info,
      work_style: answers.work_style,
      living_plan: answers.living_plan,
      user_age: answers.user_age,
    },
  };
};
